/* raleway-200 - latin */
@font-face {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 200;
    font-display: swap;
    src: url('./raleway-v14-latin-200.eot'); /* IE9 Compat Modes */
    src: local('Raleway ExtraLight'), local('Raleway-ExtraLight'),
        url('./raleway-v14-latin-200.eot?#iefix') format('embedded-opentype'),
        /* IE6-IE8 */ url('./raleway-v14-latin-200.woff2') format('woff2'),
        /* Super Modern Browsers */ url('./raleway-v14-latin-200.woff') format('woff'),
        /* Modern Browsers */ url('./raleway-v14-latin-200.ttf') format('truetype'),
        /* Safari, Android, iOS */ url('./raleway-v14-latin-200.svg#Raleway') format('svg'); /* Legacy iOS */
}
